/* for date and time */

.datepickr, .timepickr {
    background-color: greenyellow;
    text-align: left;
    width: 690px;
}

#text {
    text-align: left;
}

#bootstrapForm {
    width: 750px;
}

input[type=number] {
    -moz-appearance: textfield;
}



input::-webkit-outer-spin-button, input::-webkit-inner-spin-button {
    -webkit-appearance: none;
